var windowOrientation;
var windowWidth;
var isMobile; 

if (typeof window !== `undefined`) 
{
  // Store the window props
  windowOrientation = window.orientation;
  windowWidth = window.innerWidth;
  isMobile =  /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);

  if (isMobile) 
  {
    window.addEventListener("deviceorientation", go, true);
  }
  else 
  {
    window.addEventListener('resize', go);
  }  
}

function go() 
{
  if (typeof window !== `undefined`) 
  {
    if (isMobile) 
    {
      if (window.orientation != windowOrientation) 
      {
        windowOrientation = window.orientation
        document.location.reload(false);
      }
    } 
    else 
    {
      if (window.innerWidth != windowWidth) 
      {
        windowWidth = window.innerWidth
        document.location.reload(false);
      }
    }
  }
}

const Post = ({ data, pageContext }) => {
  const { next, prev } = pageContext;
  const post = data.markdownRemark;
  const image = post.frontmatter.cover.childImageSharp.fluid;
  const mainimage = post.frontmatter.main_image.childImageSharp.fixed;
  const title = post.frontmatter.title;
  const mt = post.frontmatter.main_text;
  const date = post.frontmatter.date;
  const html = post.html;

  var LinedynamicWidth;
  var LineDynamicBorder;
  var dynamicWidth;
  var dynamicImageHeight;
  var dynamicHeight;
  var intViewportWidth;
  var lineMargin;

  if (typeof window !== `undefined`) {
    intViewportWidth = window.innerWidth;
  }  

  if (intViewportWidth > 500) 
  {
    dynamicWidth = 'calc(100% - 40%)';
    dynamicImageHeight = 'calc(100% - 20%)';
    LinedynamicWidth = '50%';
    dynamicHeight = '50%';
    LineDynamicBorder = '1px solid white';
    lineMargin = '-25vh auto 20% 50%';
  }
  else 
  {
    dynamicWidth = '100%';
    dynamicImageHeight = '45%';
    LinedynamicWidth = '100%';
    dynamicHeight = '0%'
    LineDynamicBorder = '0px transparent';
    lineMargin = '-35vh 3% auto';
  }  

 
    // if (window.innerHeight < 600)
    // dynamicHeight = 'calc(100% + 20vh)';
    // else
    // dynamicHeight = '105vh';

  return (
  

    <Layout>

{/* <Img fluid = {image}/> */}

{/* <NavBar style = {{color: "#FFF !important"}} /> */}
      <MainInfo>    

      </MainInfo>

      <Img 
      fluid = {image} 
      //className="css-1yvmfl5-Imm" 
      imgStyle={{
        objectFit: "contain"
      }}

      style = {{position: "absolute", left:"0", top:"10vh", right: "0", margin: "auto", width:dynamicWidth,height:dynamicImageHeight}}
      />
      <Line style = {{left:dynamicHeight, width: LinedynamicWidth, borderLeft:LineDynamicBorder, margin: lineMargin}}>
      {data.markdownRemark.frontmatter.main_text}
      </Line>

      <Info1>
      "klasdk;ask"
      </Info1>

      <Info2>
      "klasdk;ask"
      </Info2>

      <Info3>
      "klasdk;ask"
      </Info3>

      <Media>
      "klasdk;ask"
      </Media>

      <GettingStarted>
      "klasdk;ask"
      </GettingStarted>

      {/* <Header title={title} cover={image} />
      <Container>
        <Content input={html} />
        <TagsBlock list={post.frontmatter.tags || []} />
      </Container> */}

    </Layout>
  );
};

export default Post;

Post.propTypes = {
  pageContext: PropTypes.shape({
    prev: PropTypes.object,
    next: PropTypes.object,
  }).isRequired,
  data: PropTypes.object.isRequired,
};

export const query = graphql`
  query($pathSlug: String!) {
    markdownRemark(frontmatter: { path: { eq: $pathSlug } }) {
      html
      frontmatter {
        date
        title
        tags
        main_text
        main_image {
          childImageSharp {
            
            fixed(              
              quality: 80 
              width: 800                        
            ) {
              ...GatsbyImageSharpFixed_noBase64
            }            
          }
        }
        cover {
          childImageSharp {
            fluid(              
              quality: 100     
              maxWidth: 1000         
            ) {
              ...GatsbyImageSharpFluid_withWebp
            }            
          }
        }

      }
    }
  }
`;
